<template>
    <div class="culture">
        <h1>Culture</h1>
        La culture française recouvre d'abord l'ensemble des pratiques culturelles qui se trouvent sur le territoire
        français. Marquée par sa diversité, sa richesse, notamment grâce à ses différentes régions, aux emprunts à
        d'autres peuples, ainsi que par sa diffusion et son influence à travers le monde, elle connaît sur de nombreux
        plans une réputation internationale.
        <hr />
        La France est depuis la fin du xiie siècle un des lieux où se sont développées de nombreuses universités créées
        et gouvernées par l'Église catholique jusqu'à la Révolution française. L'université de Paris est l'une des plus
        anciennes universités d'Occident, avec un corps de savoir qui tire ses sources des humanités de la tradition
        gréco-latine. Avec les Temps Modernes et la Réforme, ces universités deviennent ensuite des lieux où germent des
        courants intellectuels souhaitant s'affranchir de la foi, pour se refonder sur la Raison.
        <hr />
        La culture française telle qu'elle existe aujourd'hui s'est pour une grande part assise sur le rayonnement de la
        France en Europe aux xviie et xviiie siècles, alors que la langue française établissait ses lettres de noblesse
        par rapport au latin et que les européens cultivés se flattaient de la parler. Elle s'est complétée par les
        apports des populations qu'elle a côtoyées ou intégrées.
        <hr />
        Culture héritée et partagée, elle fait l'objet en France d'un département ministériel, le ministère de la
        Culture, qui succède depuis André Malraux à l'ancien secrétariat d'État aux Beaux arts, dépendant du ministère
        de l'Éducation. C'est à cette époque que le mot « culture », qui est à l'origine une notion allemande, a
        remplacé et pris le sens plus général du concept de civilisation, en réunissant le domaine des Beaux arts, à
        celui étudié par l'ethnologie, en particulier le folklore et la mythologie.
        <hr />
        Des musées consacrés à une grande variété de thèmes, dont le musée du Louvre, de riches bibliothèques, comme la
        Bibliothèque nationale de France, et tout un patrimoine immobilier, artisanal et industriel, témoignent du
        développement de la culture française.
    </div>
</template> 
   
<script>
export default {
    name: 'culture',
    metaInfo: {
        title: 'Culture de la France',
        meta: [{
            name: 'description',
            content: 'Art, litterature, cuisine et philosophie'
        }]
    }
}
</script>


