<template>
    <div class="geographie">
        <h1>Géographie</h1>
        La géographie de la France consiste en l'étude du territoire de la France, pays d'une superficie de 551 695 km2
        (région métropolitaine) et 675 417 km2 avec l'outre-mer, s'étendant sur 1 000 km du nord au sud et d'est en
        ouest (plus grande distance du nord au sud : Bray-Dunes à Cerbère). La France est le troisième plus grand pays
        d'Europe, après la Russie et l'Ukraine (le deuxième en comptant l'outre-mer). La France métropolitaine a cinq
        façades maritimes sur (du nord au sud) : la mer du Nord, la Manche, la mer d'Iroise, l'océan Atlantique et la
        mer Méditerranée. Ainsi, la longueur totale de ses côtes atteint 3 427 km.
        <hr />
        À l'exception de sa frontière nord-est, le pays est délimité principalement par des mers, l’océan et des
        frontières naturelles : le Rhin, le Jura, les Alpes, et les Pyrénées. La frontière nord-est est néanmoins
        partiellement matérialisée par les Ardennes.
    </div>
</template> 
   
<script>
export default {
    name: 'geographie',
    metaInfo: {
        title: 'Géographie de la France',
        meta: [{
            name: 'description',
            content: 'Devenez incollable sur la géographie de la France!'
        }]
    }
}
</script>


