<template>
    <div class="histoire">
        <h1>Histoire</h1>
        L'histoire de la France commence avec les premières occupations humaines du territoire correspondant au pays
        actuel. Aux groupes présents depuis le Paléolithique et le Néolithique, sont venues s'ajouter, à l'Âge du bronze
        et à l'Âge du fer, des vagues successives de Celtes, puis au iiie siècle de peuples germains (Francs, Wisigoths,
        Alamans, Burgondes) et au ixe siècle de scandinaves appelés Normands.
        <hr />
        Le nom de la France est issu d'un peuple germanique, les Francs. Clovis (466-511), roi des Francs saliens,
        scelle par son baptême à Reims l'alliance de la royauté franque avec l'Église catholique. Il unit les tribus
        franques salienne et ripuaire et conquiert un ensemble de territoires en Gaule et en Germanie qui sont agrandis
        par ses descendants mérovingiens, puis par la deuxième dynastie franque des Carolingiens fondée en 751.
        Charlemagne en particulier conquiert la Basse-Saxe dans le Nord de l'Allemagne, le royaume lombard en Italie et
        constitue une marche à l'est qui deviendra l'Autriche. L'Empire carolingien est finalement partagé en 843 entre
        ses petits-fils par le traité de Verdun qui sépare la Francie occidentale de la Francie orientale, qui deviendra
        le royaume de Germanie. La troisième dynastie franque, celle des Capétiens, s'impose définitivement en Francie
        occidentale à partir de 987. Philippe Auguste et ses successeurs donnent une nouvelle impulsion à l'unification
        territoriale du royaume de France et repoussent les frontières orientales du Rhône sur les Alpes et de la Saône
        sur le Rhin, à partir de l'achat du Dauphiné (1349) jusqu'à l'annexion de l'Alsace (1648-1681).
        <hr />
        Le nom de France n'est employé de façon officielle qu'à partir de 1190 environ, quand la chancellerie du roi
        Philippe Auguste commence à employer le terme de rex Franciæ (roi de France)1 à la place de rex Francorum (roi
        des Francs) pour désigner le souverain. Le mot était déjà couramment employé pour désigner un territoire plus ou
        moins bien défini, comme on le voit à la lecture de la Chanson de Roland, écrite un siècle plus tôt. Dès juin
        1205, le territoire est désigné dans les chartes sous le nom de regnum Franciæ, c'est-à-dire royaume de France
        en latin2,3. On ne peut ainsi parler d'histoire de France, au sens propre, et de conscience nationale française
        qu'à partir du xiie siècle4.
        <hr />
        Les Romains avaient été les premiers à unifier l'administration de la Gaule en langue latine qui est devenue
        celle de l'Église. Le concile de Tours, réuni en 813 à l'initiative de Charlemagne, impose désormais de
        prononcer les homélies dans les langues vulgaires au lieu du latin. Paris, appelée à devenir la capitale par
        l'avènement en 987 de la dynastie capétienne, devient un centre universitaire renommé. La culture française
        connaît un élan nouveau au contact de la Renaissance italienne lors des guerres d'Italie. Elle s'enrichit des
        débats sur la réforme religieuse et n'est pas par la suite étouffée comme en Italie par une contre-réforme trop
        rigoureuse. Elle éclot pleinement à compter du xviie siècle, développant un classicisme imprégné de
        cartésianisme. C'est à cette époque que le français prend sa forme moderne sous l'égide de l'Académie française.
        Le xviiie siècle est le siècle de la philosophie des Lumières, marqué par la promotion de la raison par les
        philosophes français dans les cours et capitales européennes et qui s'achève par la Révolution française.
    </div>
</template> 
   
<script>
export default {
    name: 'histoire',
    metaInfo: {
        title: 'Description générale de la France',
        meta: [{
            name: 'description',
            content: 'De Clovis à aujourd\'hui, quels sont les faits marquants de l\'histoire de la France'
        }]
    }
}
</script>


